import { Component, OnInit, Input } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { UserService } from '../users/user.service';
import { User } from '../users/user';

@Component({
  selector: 'angular-form',
  templateUrl: './form.component.html',
  styleUrls: ['./form.component.scss']
})
export class FormComponent implements OnInit {

  formularioDeCadastro: FormGroup;
  formUsers: User[];

  
  constructor( private userService: UserService ) { }

  ngOnInit() {    
    this.cadastrarUsuario();
    this.getUsers();
  }

  get nome() { return this.formularioDeCadastro.get('nome'); }
  get email() { return this.formularioDeCadastro.get('email'); }
  get tel() { return this.formularioDeCadastro.get('tel'); }


  cadastrarUsuario() {
    this.formularioDeCadastro = new FormGroup ({
      nome: new FormControl('', [ Validators.required]),
      email: new FormControl('', [ Validators.required, Validators.pattern('[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,3}$')]),
      tel: new FormControl('', [ Validators.required, Validators.pattern('[0-9]+')])
    });
  }  

  enviar() {
    if(this.formularioDeCadastro.valid) {
      const list = this.formularioDeCadastro.value;
      
      this.userService.addUser( list as User )
        .subscribe( user => {
          this.formUsers.push( user )
          console.log( user )
        })

      this.formularioDeCadastro.reset()
    }
  }

  limpar(){
    this.formularioDeCadastro.reset();
  }

  getUsers(): void {
    this.userService.getUsers()
    .subscribe( users => this.formUsers = users );
  }

  delete( user ): void {
    this.formUsers = this.formUsers.filter(h => h !== user);
    this.userService.deleteUser( user ).subscribe();
  }


}
